footer {
  background-color: #ECECEC;
  padding-top: 35px;
  padding-bottom: 20px; }
  @media screen and (max-width: 767px) {
    footer {
      padding-top: 15px;
      padding-bottom: 0; } }
  footer .footer__content {
    display: flex;
    flex-wrap: wrap;
    align-items: center; }
    @media screen and (max-width: 767px) {
      footer .footer__content {
        flex-direction: column; } }
    footer .footer__content .copyrait {
      font-size: 14px;
      line-height: 24px;
      margin-bottom: 15px; }
      @media screen and (max-width: 767px) {
        footer .footer__content .copyrait {
          order: 3; } }
    footer .footer__content .nav-footer {
      margin-bottom: 15px;
      margin-right: auto;
      margin-left: 16%; }
      @media screen and (max-width: 992px) {
        footer .footer__content .nav-footer {
          margin-left: auto; } }
      @media screen and (max-width: 767px) {
        footer .footer__content .nav-footer {
          order: 1; } }
      footer .footer__content .nav-footer ul {
        list-style-type: none;
        padding: 0;
        margin: 0;
        display: flex;
        margin-left: -20px;
        margin-right: -20px; }
        @media screen and (max-width: 576px) {
          footer .footer__content .nav-footer ul {
            flex-direction: column;
            align-items: center; } }
        footer .footer__content .nav-footer ul li {
          padding-left: 20px;
          padding-right: 20px; }
          footer .footer__content .nav-footer ul li a {
            font-size: 14px;
            line-height: 24px;
            text-decoration: none;
            color: #4C4C4C; }
            footer .footer__content .nav-footer ul li a:hover {
              text-decoration: underline; }
  footer .soc-icons {
    margin-bottom: 15px; }
    @media screen and (max-width: 767px) {
      footer .soc-icons {
        order: 2; } }

.soc-icons ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
  display: flex;
  margin-left: -14px;
  margin-right: -14px; }
  .soc-icons ul li {
    padding-left: 14px;
    padding-right: 14px; }
    .soc-icons ul li a {
      display: block;
      transition: all .25s ease; }
      .soc-icons ul li a:hover {
        opacity: 0.8; }
